


























































































































































import { Component, Vue, Watch, Prop, Ref } from 'vue-property-decorator';
import 'codemirror/theme/ambiance.css';
import { arrayFind, arrayFindDeep } from '@utils/util';
import { codemirror } from 'vue-codemirror';
import 'codemirror/theme/ambiance.css'; // 这里引入的是主题样式，根据设置的theme的主题引入，一定要引入！！
import 'codemirror/mode/javascript/javascript'; // 这里引入的模式的js，根据设置的mode引入，一定要引入！！

@Component({
  components: { codemirror }
})
export default class ParamList extends Vue {
  @Prop() flowVariables: any;
  @Prop() parameterList: any;
  @Prop() typeItem: any;
  @Prop() ignoreType: boolean;
  @Prop() tableColList: any;
  @Prop() viewmonitId: any;
  @Prop() dataViews: any;
  @Prop() formList: any;
  @Prop() fieldsView: any;
  @Prop() serviceResponseFields: any;
  @Prop() valueTypeEnum: any;
  @Prop() ignoreAdd: boolean;
  @Prop() ignoreDel: boolean;
  @Watch('fieldsView')
  getVisible(newVal: any) {
    if (newVal && newVal.length) {
      this.viewDataColList = newVal;
    }
  }
  private viewDataColList: Array<any> = [];
  private valueTypeList: Array<any> = [
    {
      value: 'form',
      label: '关联表单'
    },
    {
      value: 'variable',
      label: '流程变量'
    },
    {
      value: 'fixed',
      label: '固定值'
    },
    {
      value: 'tableSta',
      label: '表格数据'
    },
    {
      value: 'serviceResponse',
      label: '服务结果'
    },
    {
      value: 'viewData',
      label: '视图查询结果'
    }
  ];
  private defaultVFieldOptions: Array<any> = [];
  private relationList: Array<any> = [];
  private selectedForm: any = null;

  created() {
    this.refreshFromList();
    if (this.parameterList) {
      this.parameterList.forEach((item: any) => {
        if (item.value) {
          this.handleDVTagetSelect(item);
        }
      });
    }
  }

  fetchTypeList() {
    return this.valueTypeList.filter(
      (type) => this.valueTypeEnum.indexOf(type.label) > -1
    );
  }

  valueTypeChange(val) {
    if (val.targetType) {
      this.$set(val, 'value', '');
      this.$set(val, 'targetType', '');
      this.$set(val, 'bindField', '');
    }
  }
  // 参数关联表单操作
  relationListSearch(val: boolean) {
    if (val) {
      this.refreshFromList();
    }
  }
  // 过滤可选表单项列表为待选项
  refreshFromList() {
    const selectVal: Array<any> = [];
    if (this.formList) {
      this.formList.forEach((item: any) => {
        let itemDisable = false;
        if (item.type === 'upload') {
          itemDisable = true;
        }
        selectVal.push({
          value: item.key,
          label: item.label,
          type: item.type,
          disabled: itemDisable
        });
      });
      this.relationList = selectVal;
    }
  }
  handleDVTagetSelect(val) {
    /**
     * 根据所选默认值绑定目标，目标类型，值类型选择不同的绑定策略
     * 策略包括：
     * 1、目标：表单项、流程变量、固定值
     * 2、目标类型：基本值、对象
     * 3、值类型：文本、日期、数字
     */
    this.selectedForm = null;
    if (val.valueType === 'form') {
      let fieldOptions = [];
      const enumItems = ['select', 'radioGroup', 'checkboxGroup'];
      let bt: any = '';
      if (val && val.value) {
        bt = arrayFind(this.formList, 'key', val.value);
      }
      if (bt && bt.type && enumItems.indexOf(bt.type) > -1) {
        // 枚举型表单，取表单对应的数据源
        if (bt.sourceDataType === 'dataView') {
          this.selectedForm = bt;
          const options = arrayFind(this.dataViews, 'id', bt.dataSetVal);
          if (options) {
            fieldOptions = options.fields;
          }
        } else if (bt.sourceDataType === 'variable') {
          const options = arrayFindDeep(
            this.flowVariables,
            'value',
            bt.flowVisibleCode
          );
          if (options && options.children) {
            fieldOptions = options.children.map((prop) => {
              return {
                prop: prop.value,
                label: prop.label,
                fieldCode: prop.value,
                fieldName: prop.label
              };
            });
          }
        }
      }
      if (fieldOptions.length > 0) {
        if (val && val.value) {
          val.targetType = 'obj';
        }
        this.defaultVFieldOptions = fieldOptions;
      } else {
        if (val && val.value) {
          val.targetType = 'base';
        }
      }
    }
  }
  handleSelectBindField(val) {
    if (this.selectedForm && val) {
      if (!this.selectedForm.linkFields) {
        this.selectedForm.linkFields = [];
      }
      if (this.selectedForm.linkFields.indexOf(val) === -1) {
        this.selectedForm.linkFields.push(val);
      }
    }
  }
  // 表单项静态数据删除操作
  deleteTabRow(index: number, rows: any) {
    rows.splice(index, 1);
  }
  // 表单项静态数据添加操作
  addTabRow(rows: any) {
    rows.push({
      label: '',
      value: '',
      valueType: 'variable'
    });
  }
}
