





























































































































































































































































































































































































import { arrayFind, arrayFindDeep } from '@utils/util';
import { Component, Vue, Emit, Watch, Prop, Ref } from 'vue-property-decorator';
import scriptEdit from './scriptEdit.vue';
import testEditor from '../../../viewDateManagent/component/editTest.vue';
import { dataViewFields } from '@api/viewData';
import ParamList from './paramList.vue';
import { cloneDeep } from 'lodash';
import { namespace } from 'vuex-class';
const processTemplate = namespace('processTemplate');
@Component({
  components: {
    scriptEdit,
    ParamList,
    testEditor
  }
})
export default class DefaultValue extends Vue {
  @processTemplate.Getter('flowVariables') flowVariables: any;
  @processTemplate.State((state) => state.customForm) globalFormAttribute;
  @Prop() defaultValueDialogVisible: boolean;
  @Prop() bindExpress: any;
  @Prop() defaultValueConfig: any;
  @Prop() defaultValue: any;
  @Prop() itemAttribute: any;
  @Prop() dataSet: any;
  @Prop() templateSet: any;
  @Prop() formAttribute: any;
  @Prop() pullDownOrCascade: boolean;
  @Prop({ default: {} }) wholeForm: any;
  private pullDownOrCascadeValue: Array<any> = [];
  @Emit('confirmDVConfig')
  confirmDVConfig() {
    return this.defaultValueConfig;
  }
  @Emit('cancelConfig')
  cancelConfig() {}
  @Watch('defaultValueConfig')
  defaultValueChange(defaultValueConfig) {
    this.pullDownOrCascadeValue = defaultValueConfig.bindTarget.split(',');
    this.staticTableListInit();
    if (this.defaultValueConfig.bindTarget) {
      const theSet = arrayFind(
        this.linkedSerDataList,
        'id',
        this.defaultValueConfig.bindTarget
      );
      if (theSet) {
        this.bindingTargetsList = theSet.fields.map((item) => ({
          ...item
        }));
      }
    }
    if (defaultValueConfig.type === 'childFrom') {
      if (defaultValueConfig.paramDefList.length > 0) {
        this.paramDefList = defaultValueConfig.paramDefList;
      } else {
        this.paramDefList = defaultValueConfig.childFromList.formList.map(
          (item) => {
            return {
              key: item.key,
              label: item.label,
              value: ''
            };
          }
        );
      }
    }
    if (defaultValueConfig.bindType) {
      if (
        defaultValueConfig.bindType === 'form' &&
        defaultValueConfig.bindTarget
      ) {
        this.handleDVTagetSelect();
      }
    }
  }
  private paramDefList: Array<any> = [];
  private bindingTargetsList: Array<any> = [];
  private associatedLink: Array<any> = [];
  private linkedSerDataList: Array<any> = [];
  private tableShowConfig: any = {};
  private defaultVFieldOptions: Array<any> = [];
  private textValue = [];
  private staticTableList: Array<any> = [];
  private relationList: Array<any> = [];
  private selectedForm: any = null;
  created() {
    this.fromList();
    this.getDataPushSet();
  }
  // 数据视图-参数
  linkedSerValChange(val, edit) {
    if (val) {
      this.defaultValueConfig.bindTarget = val;
      const theSet = arrayFind(this.linkedSerDataList, 'id', val);
      if (theSet) {
        this.bindingTargetsList = theSet.fields.map((item) => ({
          ...item
        }));
        if (!edit) {
          const queryParams = theSet['queryParams'];
          if (queryParams) {
            const qps: Array<any> = [];
            queryParams.forEach((item) => {
              // 过滤固定值
              if (item.required !== 3) {
                qps.push({
                  ...item,
                  label: item.code,
                  valueType: '',
                  value: '',
                  targetType: 'base',
                  bindField: ''
                });
              }
            });
            this.defaultValueConfig.associatedLink = qps;
          } else {
            this.defaultValueConfig.associatedLink = [];
          }
        }
      }
    }
  }
  // 数据视图查询
  getDataPushSet() {
    dataViewFields({ type: '0' }).then(({ data, error }) => {
      if (data) {
        this.linkedSerDataList = data;
      } else {
        this.$message.error(error.message);
      }
    });
  }
  // 过滤可选表单项列表为待选项
  fromList() {
    const selectVal: Array<any> = [];
    console.log('this.wholeForm', this.wholeForm);
    if (
      this.wholeForm &&
      this.wholeForm.formList &&
      this.wholeForm.formList.length > 0
    ) {
      this.wholeForm.formList.forEach((item: any) => {
        let itemDisable = false;
        if (
          item.type === 'upload' ||
          item.type === 'table' ||
          item.key === this.itemAttribute.key
        ) {
          itemDisable = true;
        }
        if (item.type !== 'childFrom') {
          if (item.type === 'group') {
            item.value.forEach((groupItem) => {
              let groupItemDisable = false;
              if (groupItem.type === 'upload') {
                groupItemDisable = true;
              }
              selectVal.push({
                value: groupItem.key,
                label: groupItem.label,
                type: groupItem.type,
                disabled: groupItemDisable
              });
            });
          } else {
            selectVal.push({
              value: item.key,
              label: item.label,
              type: item.type,
              disabled: itemDisable
            });
          }
        } else {
          const obj: any = {
            value: item.key,
            label: item.label,
            type: item.type,
            disabled: false,
            children: []
          };
          item.value.formList.forEach((childItem: any) => {
            let childItemDisable = false;
            if (childItem.type === 'upload') {
              childItemDisable = true;
            }
            if (childItem.type === 'group') {
              childItem.value.forEach((childGroupItem) => {
                let childGroupItemDisable = false;
                if (childGroupItem.type === 'upload') {
                  childGroupItemDisable = true;
                }
                obj.children.push({
                  value: childGroupItem.key,
                  label: childGroupItem.label,
                  type: childGroupItem.type,
                  disabled: childGroupItemDisable
                });
              });
            } else {
              obj.children.push({
                value: childItem.key,
                label: childItem.label,
                type: childItem.type,
                disabled: childItemDisable
              });
            }
          });
          selectVal.push(obj);
        }
      });
    }
    this.relationList = selectVal;
  }
  staticTableListInit() {
    if (this.itemAttribute.type === 'table') {
      const selectVal: Array<any> = [];
      this.itemAttribute.cols.forEach((item: any) => {
        selectVal.push({
          value: item.fieldCode,
          label: item.fieldName,
          type: item.fieldType
        });
      });
      this.staticTableList = selectVal;
    }
  }
  confirmDVConfigSave() {
    this.defaultValueConfig.paramDefList = this.paramDefList;
    this.defaultValueConfig.bindExpress = this.bindExpress;
    this.confirmDVConfig();
  }
  handleSwitchDV(val) {
    this.defaultValueConfig.bindTarget = '';
    this.defaultValueConfig.targetType = 'base';
    this.defaultValueConfig.bindField = '';
  }
  handleDVTagetSelectInit() {
    this.defaultValueConfig.bindField = '';
    this.handleDVTagetSelect();
  }
  handleDVTagetCascaderInit(val) {
    this.defaultValueConfig.bindTarget = val.join(',');
    this.defaultValueConfig.bindField = '';
    this.handleDVTagetSelect();
  }
  dataDeal(val) {
    val.formList.forEach((item, index) => {
      //   // 处理子表单下面的同一表单的数据
      if (item.type === 'childFrom') {
        item.value.formList.forEach((childItem, childIndex) => {
          if (childItem.type === 'group') {
            childItem.value.forEach((groupChildItem, groupChildIndex) => {
              item.value.formList.splice(
                childIndex + groupChildIndex + 1,
                0,
                groupChildItem
              );
            });
            item.value.formList.splice(childIndex, 1);
          }
        });
      } else {
        if (item.type === 'group') {
          item.value.forEach((childItem, childIndex) => {
            val.formList.splice(index + childIndex + 1, 0, childItem);
          });
          val.formList.splice(index, 1);
        }
      }
    });
  }
  handleDVTagetSelect() {
    /**
     * 根据所选默认值绑定目标，目标类型，值类型选择不同的绑定策略
     * 策略包括：
     * 1、目标：表单项、流程变量、固定值
     * 2、目标类型：基本值、对象
     * 3、值类型：文本、日期、数字
     */
    this.selectedForm = null;
    let fieldOptions = [];
    const enumItems = ['select', 'radioGroup', 'checkboxGroup'];
    let bt: any = '';
    const cloneBindTarget = cloneDeep(this.defaultValueConfig.bindTarget);
    const targetArray = cloneBindTarget.split(',');
    const localForm = cloneDeep(this.globalFormAttribute);
    this.dataDeal(localForm);
    if (targetArray.length === 1) {
      bt = arrayFind(localForm.formList, 'key', targetArray[0]);
    } else if (targetArray.length > 1) {
      const fatherObj = arrayFind(localForm.formList, 'key', targetArray[0]);
      bt = arrayFind(fatherObj.value.formList, 'key', targetArray[1]);
    }
    if (bt && bt.type && enumItems.indexOf(bt.type) > -1) {
      // 枚举型表单，取表单对应的数据源
      if (bt.sourceDataType === 'dataView') {
        this.selectedForm = bt;
        const options = arrayFind(this.dataSet, 'id', bt.dataSetVal);
        if (options) {
          fieldOptions = options.fields;
          if (this.itemAttribute.type === 'date') {
            this.textValue = options.fields.filter(
              (item) => item.fieldType === 2
            );
          } else {
            this.textValue = options.fields;
          }
        }
      } else if (bt.sourceDataType === 'variable') {
        const options = arrayFindDeep(
          this.flowVariables,
          'value',
          bt.flowVisibleCode
        );
        if (options && options.children) {
          fieldOptions = options.children.map((prop) => {
            return {
              prop: prop.value,
              label: prop.label,
              fieldCode: prop.value,
              fieldName: prop.label
            };
          });
        }
      }
    }

    if (fieldOptions.length > 0) {
      this.defaultValueConfig.targetType = 'obj';
      this.defaultVFieldOptions = fieldOptions;
    } else {
      this.defaultValueConfig.targetType = 'base';
    }
  }
  handleSelectBindField(val) {
    if (this.selectedForm && val) {
      if (!this.selectedForm.linkFields) {
        this.selectedForm.linkFields = [];
      }
      if (this.selectedForm.linkFields.indexOf(val) === -1) {
        this.selectedForm.linkFields.push(val);
      }
    }
  }
}
