









































































































import { Component, Vue, Watch, Prop, Ref } from 'vue-property-decorator';
import 'codemirror/theme/ambiance.css';
import { codemirror } from 'vue-codemirror';
import 'codemirror/theme/ambiance.css'; // 这里引入的是主题样式，根据设置的theme的主题引入，一定要引入！！
import 'codemirror/mode/javascript/javascript'; // 这里引入的模式的js，根据设置的mode引入，一定要引入！！
import { namespace } from 'vuex-class';
const processTemplate = namespace('processTemplate');
import ParamList from './paramList.vue';
import { cloneDeep } from 'lodash';
import { fetchMeteorShower } from '@api/processAssignment';
@Component({
  components: { codemirror, ParamList }
})
export default class ScriptEdit extends Vue {
  @processTemplate.Getter('flowVariables') flowVariables: any;
  @Prop() itemAttribute: any;
  @Prop() typeItem: any;
  @Prop() viewmonitId: any;
  @Prop() groovyList: any;
  @Prop() fieldsView: any;
  @Prop() staticTableList: any;
  @Prop() dataSet: any;
  @Prop() formAttribute: any;
  @Prop() monitTypeList: any;
  @Prop() templateSet: any;
  @Prop() serviceResponseFields: any;
  titleScript = '';
  valueTypeEnum = ['关联表单', '流程变量', '固定值'];
  cmOptions: any = {
    value: '',
    mode: 'text/javascript',
    styleActiveLine: true,
    lineNumbers: true,
    readOnly: false,
    lineWrapping: true
  };
  private scriptTypeList: Array<any> = [
    {
      value: 'javascript',
      label: 'javascript'
    },
    {
      value: 'meteorShower',
      label: 'meteorShower'
    }
  ];
  private testParameterDialog = false;
  private testResult: any = '';
  private testParameterList: Array<any> = [];

  private textValue = [];
  valueTypeListchange() {
    if (this.monitTypeList) {
      if (this.monitTypeList === '1') {
        this.valueTypeEnum = ['流程变量'];
      } else {
        this.valueTypeEnum = ['流程变量', '视图查询结果'];
      }
    } else {
      if (this.itemAttribute.staticType) {
        if (
          this.itemAttribute.staticType === 'addStatic' ||
          this.itemAttribute.staticType === 'tableSta'
        ) {
          this.valueTypeEnum = ['关联表单', '流程变量', '固定值', '表格数据'];
        } else if (this.itemAttribute.staticType === 'tableCols') {
          this.valueTypeEnum = ['表格数据'];
        } else if (this.itemAttribute.staticType === 'colServiceFetch') {
          this.valueTypeEnum = [
            '关联表单',
            '流程变量',
            '固定值',
            '表格数据',
            '服务结果'
          ];
        } else if (this.itemAttribute.staticType === 'serviceFetch') {
          this.valueTypeEnum = ['关联表单', '流程变量', '固定值', '服务结果'];
        }
      } else {
        if (this.templateSet) {
          this.valueTypeEnum = ['关联表单', '固定值'];
        }
      }
      if (this.groovyList) {
        this.valueTypeEnum = ['流程变量', '固定值'];
      }
    }
  }
  created() {
    this.valueTypeListchange();
    this.titleChang();
    if (!this.itemAttribute.scriptType) {
      this.itemAttribute.scriptType = 'javascript';
    }
    if (this.itemAttribute.script) {
      this.itemAttribute.valueShow = this.itemAttribute.script;
    }
  }
  titleChang() {
    if (this.groovyList) {
      this.titleScript = '模板编辑器';
    } else {
      this.titleScript = '脚本编辑器';
    }
  }
  // 脚本测试
  scriptTest() {
    this.testResult = '';
    this.testParameterList = cloneDeep(this.itemAttribute.parameterList);
    this.testParameterList.forEach((item) => {
      this.$set(item, 'isJson', false);
    });
    if (this.itemAttribute.valueShow !== '') {
      if (this.testParameterList.length === 0) {
        if (this.itemAttribute.scriptType === 'javascript') {
          // 普通脚本
          this.testParamsscript([]);
        } else if (this.itemAttribute.scriptType === 'meteorShower') {
          // 刘星语脚本
          this.testMeteorShowerScript([]);
        }
      }
      this.testParameterDialog = true;
    } else {
      this.$message.warning('请输入脚本');
    }
  }
  testParameterCancel() {
    this.testParameterDialog = false;
  }
  // 测试参数编辑器 - 确定按钮
  handleTestQuery() {
    if (this.testParameterList.length > 0) {
      if (this.itemAttribute.scriptType === 'javascript') {
        // 普通脚本
        this.testParamsscript(this.testParameterList);
      } else if (this.itemAttribute.scriptType === 'meteorShower') {
        // 刘星语脚本
        this.testMeteorShowerScript(this.testParameterList);
      }
    } else {
      this.testParameterDialog = false;
    }
  }
  // 普通脚本测试
  testParamsscript(val) {
    const params: any = [];
    val.forEach((item) => {
      if (item.isJson) {
        console.log(item.value);
        params.push(JSON.parse(item.value));
      } else {
        params.push(item.value);
      }
    });
    // 参数已全部获得，执行脚本
    new Promise((resolve, reject) => {
      const methodChage = eval(
        `window.dealFormScript = ${this.itemAttribute.valueShow}`
      );
      resolve(methodChage(...params));
    }).then((result) => {
      this.testResult = result;
    });
  }
  // 刘星语脚本测试
  testMeteorShowerScript(val) {
    new Promise((resolve, reject) => {
      // 刘星语
      let meteorShower = this.itemAttribute.valueShow;
      // 替换函数中的变量占位符
      val.forEach((par: any) => {
        if (par.isJson) {
          par.value = JSON.parse(par.value);
        }
        meteorShower = meteorShower.replace(
          new RegExp(par['label'], 'g'),
          `"${par['value']}"`
        );
      });
      fetchMeteorShower({
        script: meteorShower
      }).then(({ data }: any) => {
        if (data) {
          resolve(Array.isArray(data) ? data.join(',') : data);
        } else {
          resolve('');
        }
      });
    }).then((result) => {
      this.testResult = result;
    });
  }
}
