  changeMore() {
    this.valueList();
  }
  // 全选
  handleCheckAllChange(val) {
    this.associatedNodeList = val
      ? this.itemAttribute.options.map((col) => col.fieldCode)
      : [];
    this.isIndeterminate = false;
  }
  // 单选
  handleCheckedCitiesChange(value) {
    const checkedCount = value.length;
    this.checkAll = checkedCount === this.itemAttribute.options.length;
    this.isIndeterminate =
      checkedCount > 0 && checkedCount < this.itemAttribute.options.length;
  }
  openMailDialog() {
    this.rghMenu.init(); // 组件右键菜单初始化
  }
  tableButChange(val) {
    this.targetTableColList = [];
    const linkTableFrom = arrayFind(this.formAttribute.formList, 'key', val);
    if (linkTableFrom.type === 'table') {
      this.targetTableColList = linkTableFrom.cols;
    }
  }
  // 表格操作列关联服务确定
  linkedServiceSave() {
    const setLink = {
      linkedSerVal: this.linkedSerVal,
      associatedLink: this.associatedLink,
      scriptLinkConfig: this.scriptLinkConfig,
      targetObjectBut: this.targetObjectBut
    };
    this.scopeRow.setLink = setLink;
    this.linkedServiceVisible = false;
  }
  // 表格操作列关联服务
  linkedService(row) {
    this.targetColListQuery();
    if (!row.setLink) {
      this.linkedSerVal = '';
      this.associatedLink = [];
      this.scriptLinkConfig = {
        scriptType: 'javascript',
        staticType: 'addStatic',
        valueShow: '',
        parameterList: []
      };
      this.targetObjectBut = {
        configTarget: 'tableColBtn',
        targetCol: '',
        setWay: 'bind',
        activeName: 'first',
        valueTypeEnum: ['关联表单', '表格数据', '流程变量', '固定值'],
        bindingTargets: '',
        scriptTargets: {
          scriptType: 'javascript',
          staticType: 'colServiceFetch',
          valueShow: '',
          parameterList: []
        }
      };
    } else {
      const scope = cloneDeep(row.setLink);
      this.linkedSerVal = scope.linkedSerVal;
      this.associatedLink = scope.associatedLink;
      this.scriptLinkConfig = scope.scriptLinkConfig;
      this.targetObjectBut = scope.targetObjectBut;
      this.linkedSerValChange(this.linkedSerVal, 'edit');
    }
    this.linkedServiceVisible = true;
    this.scopeRow = row;
  }
  // 按钮关联服务
  linkFormBtnService() {
    if (!this.itemAttribute.setLink) {
      this.linkedSerVal = '';
      this.associatedLink = [];
      this.scriptLinkConfig = {
        scriptType: 'javascript',
        valueShow: '',
        parameterList: []
      };
      this.targetObjectBut = {
        configTarget: 'formBtn',
        targetCol: '',
        targetTableCol: '',
        setWay: 'bind',
        activeName: 'first',
        valueTypeEnum: ['关联表单', '流程变量', '固定值'],
        bindingTargets: '',
        scriptTargets: {
          scriptType: 'javascript',
          staticType: 'serviceFetch',
          valueShow: '',
          parameterList: []
        }
      };
    } else {
      const scope = cloneDeep(this.itemAttribute.setLink);
      this.linkedSerVal = scope.linkedSerVal;
      this.associatedLink = scope.associatedLink;
      this.scriptLinkConfig = scope.scriptLinkConfig;
      this.targetObjectBut = scope.targetObjectBut;
      this.linkedSerValChange(this.linkedSerVal, 'edit');
    }
    this.linkedServiceVisible = true;
    this.scopeRow = this.itemAttribute;
  }
  // 添加子表单
  openformValueConfig(row, str, index) {
    this.tabIndex = index;
    this.tabRow = row;
    this.childKey = this.itemAttribute.key;
    this.cldFormBtn = true;
    this.chengeFrom = true;
    this.addLine = false;
    this.$emit('fromChange', this.itemAttribute, this.tabRow);
  }
  // 子表单保存
  childSave() {
    this.cldFormBtn = false;
    this.chengeFrom = false;
    this.addLine = true;
    const normalChildForm = cloneDeep(this.formAttribute);
    normalChildForm.formList.forEach((item, index) => {
      if (item.type === 'group') {
        item.value.forEach((childItem, childIndex) => {
          this.$set(childItem, 'parentKey', item.key);
          this.$set(childItem, 'parentType', 'group');
          this.$set(childItem, 'parentLable', item.label);
          normalChildForm.formList.splice(index + childIndex + 1, 0, childItem);
        });
        normalChildForm.formList.splice(index, 1);
      }
    });
    this.$emit('childSave', normalChildForm, this.childKey, this.tabIndex);
    this.childKey = '';
  }
  // 子表单取消
  childCancle() {
    this.childKey = '';
    this.cldFormBtn = false;
    this.chengeFrom = false;
    this.addLine = true;
    this.$emit('childCancle');
  }
  // 字段属性中的标题修改
  itemLabelChange() {
    const AttributeList: Array<any> = [];
    AttributeList.push(this.itemAttribute);
    if (
      this.itemAttribute.label ||
      (this.itemAttribute.value && this.itemAttribute.type === 'button')
    ) {
      const sa = Math.floor(Math.random() * 90) + 10;
      if (AttributeList.length > 1) {
        if (this.itemAttribute.type === 'button') {
          if (this.itemAttribute.value === '') {
            this.itemAttribute.key = sa;
          } else {
            this.itemAttribute.key = pinyin4js
              .convertToPinyinString(
                this.itemAttribute.value,
                '',
                pinyin4js.WITHOUT_TONE
              )
              .concat(sa.toString());
          }
        } else {
          this.itemAttribute.key = pinyin4js
            .convertToPinyinString(
              this.itemAttribute.label,
              '',
              pinyin4js.WITHOUT_TONE
            )
            .concat(sa.toString());
        }
      } else {
        if (this.itemAttribute.type === 'button') {
          if (this.itemAttribute.value === '') {
            this.itemAttribute.key = sa;
          } else {
            this.itemAttribute.key = pinyin4js.convertToPinyinString(
              this.itemAttribute.value,
              '',
              pinyin4js.WITHOUT_TONE
            );
          }
        } else {
          this.itemAttribute.key = pinyin4js.convertToPinyinString(
            this.itemAttribute.label,
            '',
            pinyin4js.WITHOUT_TONE
          );
          const pattern = /[`~!@#$^&*()=+%|{}':;',[\]._<>/?~！@#￥……&*（）——|{}【】'“” ‘’；：""'。，、？-\s]/g;
          this.itemAttribute.key = this.itemAttribute.key.replace(pattern, '');
        }
      }
    }
  }
  // 属性值设置
  labelChange(record) {
    const propChan = pinyin4js.convertToPinyinString(
      record.fieldName,
      '',
      pinyin4js.WITHOUT_TONE
    );
    this.$set(record, 'fieldCode', propChan);
  }
  // tabs标签属性值设置
  tabsLabelChange(record) {
    const propChan = pinyin4js.convertToPinyinString(
      record.label,
      '',
      pinyin4js.WITHOUT_TONE
    );
    this.$set(record, 'value', propChan);
  }
  // 静态列类型change
  typeeStaticChange(val) {
    this.$set(val, 'value', '');
  }
  // 静态列设置
  staticColConfig(val) {
    if (!val.defaultList) {
      this.defaultValueConfig = {
        cfgType: 'bind',
        bindType: 'form',
        bindTarget: '',
        scriptType: 'javascript',
        targetType: 'base',
        bindField: '',
        staticType: 'addStatic',
        type: '',
        valueShow: '',
        parameterList: [],
        bindExpress: {}
      };
      this.defaultValueConfig.fieldType = val.fieldType.toString();
      this.defaultValueConfig.fieldCode = val.fieldCode;
      this.defaultValueConfig.type = this.itemAttribute.type;
      if (val.fieldType.toString() === '2') {
        this.bindExpress = {
          year: 0,
          month: 0,
          day: 0,
          yearSign: 0,
          monthSign: 0,
          daySign: 0
        };
      } else if (val.fieldType.toString() === '1') {
        this.bindExpress = {
          offset: 0,
          sign: 0
        };
      }
      this.colStatic = val;
    } else {
      this.defaultValueConfig = cloneDeep(val.defaultList);
      this.bindExpress = val.bindExpress;
    }
    this.defaultValueDialogVisible = true;
  }
  commonTableColScript(val) {
    if (!val.defaultList) {
      this.defaultValueConfig = {
        cfgType: 'script',
        bindType: 'fix',
        bindTarget: '',
        scriptType: 'javascript',
        targetType: 'base',
        bindField: '',
        staticType: 'tableSta',
        type: 'table',
        valueShow: '',
        parameterList: [],
        bindExpress: {}
      };
      this.defaultValueConfig.fieldType = val.fieldType
        ? val.fieldType.toString()
        : '0';
      this.defaultValueConfig.fieldCode = val.fieldCode;
      this.colStatic = val;
    } else {
      val.defaultList.cfgType = 'script';
      val.defaultList.staticType = 'tableSta';
      this.defaultValueConfig = cloneDeep(val.defaultList);
    }
    this.tableScriptSetVisible = true;
  }
  saveCommonTableColScript() {
    const col = arrayFind(
      this.itemAttribute.cols,
      'fieldCode',
      this.defaultValueConfig.fieldCode
    );
    if (col) {
      col.defaultList = this.defaultValueConfig;
    }
    this.tableScriptSetVisible = false;
  }
  /**
   *显示并设置表格的表单项的设置
   * @param   {[object]}  val  [val 表单项的设置]
   */
  addTableFormConfig(row) {
    this.showTableFormDialog = true;
    if (!row.tableFormConfig) {
      this.$set(row, 'tableFormConfig', cloneDeep(this.tableFormInitConfig));
    }
    this.tableFormConfig = row.tableFormConfig;
    this.scopeRow = row;
  }
  saveTabelFormConfig() {
    this.showTableFormDialog = false;
  }
  handleSwitchDV() {
    this.defaultValueConfig.bindTarget = '';
    this.defaultValueConfig.targetType = 'base';
    this.defaultValueConfig.bindField = '';
  }
  cancelConfig() {
    this.defaultValueDialogVisible = false;
  }
  openDefaultValueConfig() {
    if (!this.itemAttribute.defaultValue) {
      if (this.itemAttribute.type === 'table') {
        this.defaultValueConfig = {
          cfgType: 'script',
          bindType: 'form',
          bindTarget: '',
          scriptType: 'javascript',
          targetType: 'base',
          bindField: '',
          valueShow: '',
          type: '',
          parameterList: [],
          bindExpress: {}
        };
      } else {
        this.defaultValueConfig = {
          cfgType: 'bind',
          bindType: 'form',
          bindTarget: '',
          scriptType: 'javascript',
          targetType: 'base',
          bindField: '',
          valueShow: '',
          paramDefList: [],
          type: '',
          checked: false,
          parameterList: [],
          bindExpress: {},
          valueTypeEnum: ['关联表单', '流程变量', '固定值'],
          associatedLink: [],
          scriptLinkConfig: {
            scriptType: 'javascript',
            valueShow: '',
            parameterList: []
          },
          activeName: 'first',
          targetObjectBut: {
            targetCol: '',
            setWay: 'bind',
            bindingTargets: '',
            scriptTargets: {
              scriptType: 'javascript',
              staticType: 'serviceFetch',
              valueShow: '',
              parameterList: []
            }
          }
        };
        this.defaultValueConfig.type = this.itemAttribute.type;
        if (this.itemAttribute.type === 'date') {
          this.bindExpress = {
            year: 0,
            month: 0,
            day: 0,
            yearSign: 0,
            monthSign: 0,
            daySign: 0
          };
        } else if (this.itemAttribute.type === 'number') {
          this.bindExpress = {
            offset: 0,
            sign: 0
          };
        } else if (this.itemAttribute.type === 'childFrom') {
          this.defaultValueConfig.childFromList = this.itemAttribute.value;
        }
      }
    } else {
      if (this.itemAttribute.defaultValue.parameterList) {
        this.scriptList = this.itemAttribute.defaultValue.parameterList;
      } else {
        this.scriptList = [];
      }
      this.defaultValueConfig = cloneDeep(this.itemAttribute.defaultValue);
      this.bindExpress = this.defaultValueConfig.bindExpress;
      if (!this.itemAttribute.defaultValue.associatedLink) {
        this.defaultValueConfig.associatedLink = [];
        this.defaultValueConfig.scriptLinkConfig = {
          scriptType: 'javascript',
          staticType: 'addStatic',
          valueShow: '',
          parameterList: []
        };
        this.defaultValueConfig.valueTypeEnum = [
          '关联表单',
          '流程变量',
          '固定值'
        ];
        this.defaultValueConfig.activeName = 'first';
        this.defaultValueConfig.targetObjectBut = {
          targetCol: '',
          setWay: 'bind',
          bindingTargets: '',
          scriptTargets: {
            scriptType: 'javascript',
            staticType: 'colServiceFetch',
            valueShow: '',
            parameterList: []
          }
        };
      }
    }
    this.defaultValueDialogVisible = true;
    if (
      this.defaultValueConfig.bindType === 'form' &&
      this.defaultValueConfig.bindTarget
    ) {
      this.handleCTagetSelect(
        this.defaultValueConfig,
        this.defaultValueConfig.bindTarget,
        true
      );
    }
  }
  confirmDVConfig(val) {
    if (val.type === 'table') {
      this.currentAddSet.colsAdd.forEach((item: any) => {
        if (item.fieldCode === val.fieldCode) {
          item.defaultList = val;
        }
      });
    } else {
      this.itemAttribute.defaultValue = val;
    }
    this.defaultValueDialogVisible = false;
  }

  handleCTagetSelect(config, val, edit) {
    if (!this.pullDownOrCascade) {
      val = val.join(',');
      this.showValueConfig.valueShow = val;
      this.showValueConfig.isChildForm = true;
    } else {
      this.showValueConfig.isChildForm = false;
    }
    if (!edit) {
      config.bindField = '';
    }
    const fieldOptions = this.getFieldOptions(val);
    if (fieldOptions.length > 0) {
      config.targetType = 'obj';
      this.defaultVFieldOptions = fieldOptions;
    } else {
      config.targetType = 'base';
    }
  }

  /**
   * 根据所选默认值绑定目标，目标类型，值类型选择不同的绑定策略
   * 策略包括：
   * 1、目标：表单项、流程变量、固定值
   * 2、目标类型：基本值、对象
   * 3、值类型：文本、日期、数字
   */
  getFieldOptions(val) {
    let fieldOptions = [];
    const enumItems = ['select', 'radioGroup', 'checkboxGroup'];

    const bt: any = arrayFind(this.formAttribute.formList, 'key', val);

    if (bt && bt.type && enumItems.indexOf(bt.type) > -1) {
      // 枚举型表单，取表单对应的数据源
      if (bt.sourceDataType === 'dataView') {
        const options = arrayFind(this.dataSet, 'id', bt.dataSetVal);
        if (options) {
          fieldOptions = options.fields;
        }
      } else if (bt.sourceDataType === 'variable') {
        const options = arrayFindDeep(
          this.flowVariables,
          'value',
          bt.flowVisibleCode
        );
        if (options && options.children) {
          fieldOptions = options.children.map((prop) => {
            return {
              prop: prop.value,
              label: prop.label,
              fieldCode: prop.value,
              fieldName: prop.label
            };
          });
        }
      }
    }

    return fieldOptions;
  }
  // 表单项静态数据删除操作
  deleteTabRow(index: number, rows: any) {
    rows.splice(index, 1);
  }
  // 表单项静态数据添加操作  按钮添加操作 //
  addTabRow(rows: any, otherName: string) {
    let obj: any = null;
    if (otherName === 'addSetName') {
      obj = {
        addViewData: '',
        addDataLink: ''
      };
    } else if (otherName === 'staticCol') {
      obj = {
        fieldCode: '',
        fieldName: '',
        fieldType: 0,
        value: '',
        type: 'static'
      };
    } else if (otherName === 'bottonCol') {
      obj = {
        fieldCode: '',
        fieldName: '',
        fieldType: '',
        value: '',
        type: 'button'
      };
    } else if (otherName === 'filterSet') {
      obj = {
        textLinkValue: '',
        textLinkMethod: '1',
        filterType: 'form',
        textLink: ''
      };
    } else if (otherName === 'staticDataList') {
      obj = {
        staticCheck: '',
        staticFilter: '',
        staticText: ''
      };
    } else if (otherName === 'tabsList') {
      obj = {
        label: '',
        value: '',
        tabForm: {}
      };
    } else {
      obj = {
        label: '',
        value: ''
      };
    }
    rows.push(obj);
  }
  linkedSerValChange(val, edit) {
    if (val) {
      this.linkedSerVal = val;
      const theSet = arrayFind(this.linkedSerDataList, 'id', val);
      if (theSet) {
        this.bindingTargetsList = theSet.fields.map((item) => ({
          ...item
        }));

        if (!edit) {
          const queryParams = theSet['queryParams'];
          if (queryParams) {
            const qps: Array<any> = [];
            queryParams.forEach((item) => {
              // 过滤固定值
              if (item.required !== 3) {
                qps.push({
                  ...item,
                  label: item.code,
                  valueType: '',
                  value: '',
                  targetType: 'base',
                  bindField: ''
                });
              }
            });
            this.associatedLink = qps;
          } else {
            this.associatedLink = [];
          }
        }
      }
    }
  }
  /**
   * 表格表单配置的数据视图选择数据处理
   */
  tableFormDataSetChange(val: any) {
    this.tableFormOptionLabel = [];
    this.tableFormConfig.options = [];
    if (val) {
      this.tableFormConfig.dataSetVal = val;
      const theSet = arrayFind(this.dataSet, 'id', val);
      if (theSet) {
        const queryParams = theSet['queryParams'];
        this.tableFormConfig.dataQueryList = queryParams
          ? queryParams.map((item) => {
              if (item.required != 3) {
                item = {
                  ...item,
                  ...{
                    label: item.code,
                    valueType: '',
                    value: '',
                    targetType: 'base',
                    bindField: ''
                  }
                };
                return item;
              }
            })
          : [];
      }
      this.tableFormConfig.dictFlagValue = theSet.dictFlag;
      this.tableFormOptionLabel = theSet['fields'];
    }
  }
  // 数据视图选择数据处理
  dataSetChange(val: any) {
    this.itemAttribute.cols = [];
    this.itemAttribute.associatedNodeList = [];
    this.itemAttribute.options = [];
    if (val) {
      this.itemAttribute.dataSetVal = val;
      this.itemAttribute.origId = val;
      const theSet = arrayFind(this.dataSet, 'id', val);
      if (theSet) {
        const queryParams = theSet['queryParams'];
        if (queryParams) {
          const qps: Array<any> = [];
          queryParams.forEach((item) => {
            // 过滤固定值
            if (item.required !== 3) {
              qps.push({
                ...item,
                label: item.code,
                valueType: '',
                value: '',
                targetType: 'base',
                bindField: ''
              });
            }
          });
          this.itemAttribute.dataQueryList = qps;
        } else {
          this.itemAttribute.dataQueryList = [];
        }
        this.itemAttribute.dictFlagValue = theSet.dictFlag;
        this.optionLabel = theSet['fields'];
        if (this.itemAttribute.type === 'table') {
          this.getDataSetFields(
            theSet.fields.map((col) => {
              return {
                ...col,
                prop: col.fieldCode,
                label: col.fieldName
              };
            })
          );
        } else {
          this.itemAttribute.cols = theSet.fields.map((col) => {
            return {
              ...col,
              prop: col.fieldCode,
              label: col.fieldName
            };
          });
        }
      }
    }
  }

  // 根据数据集ID查询其字段列表
  getDataSetFields(fields) {
    if (fields) {
      // 如果切换了主表数据，关于列的所有的配置都会被重置
      this.itemAttribute.options = cloneDeep(fields);
      this.itemAttribute.associatedNodeList = fields.map(
        (fie) => fie.fieldCode
      );
      this.itemAttribute.cols = cloneDeep(fields);
      this.itemAttribute.addSetList = [];
      this.itemAttribute.mutexSetList = [];
    } else {
      this.itemAttribute.cols = [];
      this.$set(this.itemAttribute, 'associatedNodeList', []);
    }
  }

  valueShowListSearch(val: boolean) {
    if (val) {
      this.valueList();
    }
  }
  valueList() {
    const selectVal: Array<any> = [];
    if (this.chengeFrom === false) {
      this.localFormList = this.formAttribute.formList;
      sessionStorage.setItem(
        'sessionSelectVal',
        JSON.stringify(this.localFormList)
      );
    } else {
      const str: any = sessionStorage.getItem('sessionSelectVal');
      this.localFormList = JSON.parse(str);
    }
    this.localFormList.forEach((item: any) => {
      let itemDisable = false;
      if (item.key === this.itemAttribute.key) {
        itemDisable = true;
      }
      if (item.type !== 'childFrom') {
        if (item.type === 'group') {
          item.value.forEach((groupItem) => {
            let groupItemDisable = false;
            if (groupItem.type === 'upload') {
              groupItemDisable = true;
            }
            selectVal.push({
              value: groupItem.key,
              label: groupItem.label,
              type: groupItem.type,
              disabled: groupItemDisable
            });
          });
        } else {
          selectVal.push({
            value: item.key,
            label: item.label,
            type: item.type,
            disabled: itemDisable
          });
        }
      } else {
        const obj: any = {
          value: item.key,
          label: item.label,
          type: item.type,
          disabled: itemDisable,
          children: []
        };
        item.value.formList.forEach((childItem: any) => {
          let childItemDisable = false;
          if (childItem.type === 'upload') {
            childItemDisable = true;
          }
          if (childItem.type === 'group') {
            childItem.value.forEach((childGroupItem) => {
              let childGroupItemDisable = false;
              if (childGroupItem.type === 'upload') {
                childGroupItemDisable = true;
              }
              obj.children.push({
                value: childGroupItem.key,
                label: childGroupItem.label,
                type: childGroupItem.type,
                disabled: childGroupItemDisable
              });
            });
          } else {
            obj.children.push({
              value: childItem.key,
              label: childItem.label,
              type: childItem.type,
              disabled: childItemDisable
            });
          }
        });
        selectVal.push(obj);
      }
    });
    const flag: any = selectVal.some((item: any) => {
      return item.type === 'childFrom';
    });
    if (flag) {
      this.pullDownOrCascade = false;
    } else {
      this.pullDownOrCascade = true;
    }
    this.valueShowList = selectVal;
  }
  // 过滤可选表单项列表为待选项
  fromList() {
    const selectVal: Array<any> = [];
    this.formAttribute.formList.forEach((item: any) => {
      let itemDisable = false;
      if (
        item.type === 'upload' ||
        item.type === 'table' ||
        item.key === this.itemAttribute.key
      ) {
        itemDisable = true;
      }
      if (item.type !== 'childFrom') {
        if (item.type === 'group') {
          item.value.forEach((groupItem) => {
            let groupItemDisable = false;
            if (groupItem.type === 'upload') {
              groupItemDisable = true;
            }
            selectVal.push({
              value: groupItem.key,
              label: groupItem.label,
              type: groupItem.type,
              disabled: groupItemDisable
            });
          });
        } else {
          selectVal.push({
            value: item.key,
            label: item.label,
            type: item.type,
            disabled: itemDisable
          });
        }
      } else {
        const obj: any = {
          value: item.key,
          label: item.label,
          type: item.type,
          disabled: itemDisable,
          children: []
        };
        item.value.formList.forEach((childItem: any) => {
          let childItemDisable = false;
          if (childItem.type === 'upload') {
            childItemDisable = true;
          }
          if (childItem.type === 'group') {
            childItem.value.forEach((childGroupItem) => {
              let childGroupItemDisable = false;
              if (childGroupItem.type === 'upload') {
                childGroupItemDisable = true;
              }
              obj.children.push({
                value: childGroupItem.key,
                label: childGroupItem.label,
                type: childGroupItem.type,
                disabled: childGroupItemDisable
              });
            });
          } else {
            obj.children.push({
              value: childItem.key,
              label: childItem.label,
              type: childItem.type,
              disabled: childItemDisable
            });
          }
        });
        selectVal.push(obj);
      }
      // }
    });
    const flag: any = selectVal.some((item: any) => {
      return item.type === 'childFrom';
    });
    if (flag) {
      this.pullDownOrCascadeTwo = false;
    } else {
      this.pullDownOrCascadeTwo = true;
    }
    this.relationList = selectVal;
  }
  // 参数关联表单操作
  relationListSearch(val: boolean) {
    if (val) {
      this.fromList();
    }
  }
  /**
   * 获取表格表单配置的流程变量
   */
  handleTableFormFlowVariable(val: any) {
    this.tableFormOptionLabel = [];
    this.tableFormConfig.options = [];
    if (val !== '') {
      // 查询该流程变量及子级
      const fv = arrayFindDeep(this.flowVariables, 'value', val);
      if (fv && fv.children) {
        this.tableFormOptionLabel = fv.children.map((prop) => {
          const aliasProp = prop.value || prop.prop;
          const value = aliasProp.substring(aliasProp.lastIndexOf('.') + 1);
          return {
            prop: value,
            label: prop.label,
            fieldCode: value,
            fieldName: prop.label,
            isPrimaryKey: prop.isPrimaryKey,
            fieldType: prop.fieldType
          };
        });
        this.tableFormConfig.origId = fv.origId;
      }
    }
  }
  handleFlowVariable(val: any) {
    this.optionLabel = [];
    this.itemAttribute.cols = [];
    this.itemAttribute.associatedNodeList = [];
    this.itemAttribute.options = [];
    if (val !== '') {
      // 查询该流程变量及子级
      const fv = arrayFindDeep(this.flowVariables, 'value', val);
      if (fv && fv.children) {
        const columns = fv.children.map((prop) => {
          // 拆分流程变量的code
          const aliasProp = prop.value ? prop.value : prop.prop;
          const value = aliasProp.substring(aliasProp.lastIndexOf('.') + 1);
          return {
            prop: value,
            label: prop.label,
            fieldCode: value,
            fieldName: prop.label,
            isPrimaryKey: prop.isPrimaryKey,
            fieldType: prop.fieldType
          };
        });
        this.optionLabel = columns;
        this.itemAttribute.origId = fv.origId;
        this.getDataSetFields(columns);
      }
    }
  }
  // 表单字段关联
  correlationField(data: any) {
    this.associatedNode = true;
    this.associatedNodeList = cloneDeep(this.itemAttribute.associatedNodeList);
  }
  // 打开添加列
  addExtendSet() {
    this.extendColFlag = 'add';
    this.currentAddSet = {
      extendColName: `拓展_${this.itemAttribute.addSetList.length}`,
      addDataSetVal: '',
      addDataQueryList: [],
      addDataValue: [],
      addSetType: '',
      tabList: [],
      addViewDataLists: [],
      colsAdd: [],
      buttonAdd: []
    };
    this.linkedSerVal = '';
    this.associatedLink = [];
    this.scriptLinkConfig = {
      scriptType: 'javascript',
      staticType: '',
      valueShow: '',
      parameterList: []
    };
    this.extendColDetail = true;
    this.targetColListQuery();
  }
  targetColListQuery() {
    const selectVal: Array<any> = [];
    this.itemAttribute.cols.forEach((item: any) => {
      selectVal.push({
        value: item.fieldCode,
        label: item.fieldName,
        type: item.fieldType
      });
    });
    this.targetColList = selectVal;
  }
  handleExtendSetEdit(es, index) {
    this.extendColFlag = 'modify';
    this.currentAddSet = cloneDeep(es);
    this.modifyIndex = index;
    // 回显配置数据
    if (this.currentAddSet.addSetType === 'addViewList') {
      this.addDataView(this.currentAddSet.addDataSetVal, true);
    } else if (this.currentAddSet.addSetType === 'addFlowVariables') {
      this.handleFlowVSelected(this.currentAddSet.addDataSetVal, true);
    }
    if (this.currentAddSet.colsAdd.length > 0) {
      this.currentAddSet.colsAdd.forEach((itemCols) => {
        if (!Object.prototype.hasOwnProperty.call(itemCols, 'staticList')) {
          this.$set(itemCols, 'staticList', {
            staticType: 'tableSta',
            staticDataList: [],
            showValueConfig: {
              showType: 'tableSta',
              valueShow: '',
              targetType: 'base',
              bindField: '',
              symbolShow: '1',
              inputValue: '',
              relationship: '2',
              parameterList: [],
              staticType: 'tableCols'
            }
          });
        }
      });
    }

    this.extendColDetail = true;
  }
  handleExtendSetdel(index) {
    this.$confirm('确定要删除该拓展列设置吗?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning',
      center: true
    })
      .then(() => {
        const beDeled = this.itemAttribute.addSetList.splice(index, 1)[0];
        // 删除该拓展列下的字段
        this.itemAttribute.cols = differenceBy(
          this.itemAttribute.cols,
          beDeled.colsAdd,
          'fieldCode'
        );
      })
      .catch(() => {});
  }
  // 打开添加列
  addMutexSet() {
    if (!this.itemAttribute.mutexSetList) {
      this.itemAttribute.mutexSetList = [];
    }
    this.extendColFlag = 'add';
    this.currentAddSet = {
      extendColName: `来源_${this.itemAttribute.mutexSetList.length}`,
      addDataSetVal: '',
      addDataQueryList: [],
      addDataValue: [],
      addSetType: '',
      tabList: [],
      addViewDataLists: [],
      colsAdd: [],
      buttonAdd: []
    };
    this.mutexSetDetail = true;
  }
  handleMutexSetEdit(es, index) {
    this.extendColFlag = 'modify';
    this.currentAddSet = cloneDeep(es);
    this.modifyIndex = index;
    this.handleMutexSelected(this.currentAddSet.addDataSetVal, true);
    this.mutexSetDetail = true;
  }
  handleMutexSetSave() {
    if (this.extendColFlag === 'add') {
      this.itemAttribute.mutexSetList.push(this.currentAddSet);
    } else {
      this.itemAttribute.mutexSetList.splice(
        this.modifyIndex,
        1,
        this.currentAddSet
      );
    }
    this.mutexSetDetail = false;
  }
  handleMutexSetdel(index) {
    this.$confirm('确定要删除该数据源吗?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning',
      center: true
    })
      .then(() => {
        this.itemAttribute.mutexSetList.splice(index, 1);
      })
      .catch(() => {});
  }
  requiredChange(val: any) {
    this.itemAttribute.rules = [
      {
        required: val,
        message: this.itemAttribute.label + '不得为空',
        trigger: 'change'
      },
      {
        required: val,
        message: this.itemAttribute.label + '不得为空',
        trigger: 'blur'
      }
    ];
  }

  // 取消按钮，离开此页面
  formCancle() {
    this.$router.push({
      name: 'processNode',
      query: {
        id: this.$route.query.id ? this.$route.query.id : '',
        flag: '3', // 表示从表单配置页返回
        goback: 'true'
      }
    });
  }

  // 保存按钮，保存自定义表单
  formSave() {
    console.log(this.formAttribute);
    const normalForm = cloneDeep(this.formAttribute);
    normalForm.formList.forEach((item, index) => {
      if (item.type === 'group') {
        item.value.forEach((childItem, childIndex) => {
          this.$set(childItem, 'parentKey', item.key);
          this.$set(childItem, 'parentType', 'group');
          this.$set(childItem, 'parentLable', item.label);
          normalForm.formList.splice(index + childIndex + 1, 0, childItem);
        });
        normalForm.formList.splice(index, 1);
      }
    });
    this.$store.dispatch('processTemplate/getCustomForm', normalForm);
    this.$store.dispatch(
      'processTemplate/getFormItem',
      Base64.encode(JSON.stringify(normalForm))
    );
    if (this.currentStatus === 'edit') {
      const params = {
        tempId: this.$store.state.processTemplate.currentTempId,
        content: Base64.encode(JSON.stringify(normalForm))
      };
      editTemp(params).then(({ data, error }) => {
        if (!error) {
          this.$message.success('自定义表单已成功保存');
          this.formCancle();
        } else {
          this.$message.error(error.message);
        }
      });
    } else {
      this.formCancle();
    }
  }
  handleChecked() {
    this.associatedNode = false;
    this.itemAttribute.associatedNodeList = this.associatedNodeList;

    const colsList: Array<any> = [];
    this.itemAttribute.associatedNodeList.forEach((item) => {
      const theCol = arrayFind(this.itemAttribute.options, 'fieldCode', item);
      if (theCol) {
        colsList.push(theCol);
      }
    });
    this.itemAttribute.cols = this.polymerizeTableCols(
      this.itemAttribute.options,
      colsList,
      cloneDeep(this.itemAttribute.cols)
    );
  }
  polymerizeTableCols(originCols, selctedCols, targetCols) {
    targetCols.forEach((item, index): any => {
      selctedCols.forEach((key, ind) => {
        if (item.fieldCode === key.fieldCode) {
          targetCols.splice(index, 1, key);
        }
      });
    });
    // 加入显示隐藏中新显示的部分
    const unionCols = unionBy(targetCols, selctedCols, 'fieldCode');
    // 删除显示隐藏中隐藏掉的部分
    remove(unionCols, (col) => {
      return (
        arrayFind(originCols, 'fieldCode', col['fieldCode']) &&
        !arrayFind(selctedCols, 'fieldCode', col['fieldCode'])
      );
    });
    unionCols.forEach((colItem) => {
      if (!Object.prototype.hasOwnProperty.call(colItem, 'prop')) {
        this.$set(colItem, 'prop', colItem['fieldCode']);
        this.$set(colItem, 'label', colItem['fieldName']);
      }
    });
    return unionCols;
  }

  addDataViewList(val: boolean) {
    if (val) {
      this.viewDataSet.forEach((itemList: any) => {
        this.$set(itemList, 'disabled', false);
        if (itemList.id === this.itemAttribute.dataSetVal) {
          this.$set(itemList, 'disabled', true);
        }
      });
    }
  }
  // 添加列数据视图
  addDataView(val: string, edit: boolean) {
    if (val !== '') {
      const theSet = arrayFind(this.dataSet, 'id', val);
      if (theSet) {
        this.addOptionList = theSet['fields'];
        if (!edit) {
          this.currentAddSet.addDataValue = [];
          this.currentAddSet.addDataSetVal = val;
          const queryParams = theSet['queryParams'];
          if (queryParams) {
            const qps: Array<any> = [];
            queryParams.forEach((item) => {
              // 过滤固定值
              if (item.required !== 3) {
                qps.push({
                  ...item,
                  label: item.code,
                  valueType: '',
                  value: '',
                  targetType: 'base',
                  bindField: ''
                });
              }
            });
            this.currentAddSet.addDataQueryList = qps;
          } else {
            this.currentAddSet.addDataQueryList = [];
          }
        } else {
          this.scopeForm(true);
        }
      }
    }
  }

  handleFlowVSelected(val: string, edit: boolean) {
    if (val !== '') {
      if (!edit) {
        this.currentAddSet.addDataValue = [];
        this.currentAddSet.addDataSetVal = val;
      } else {
        this.scopeForm(true);
      }
      const fv = arrayFindDeep(this.flowVariables, 'value', val);
      if (fv && fv.children) {
        this.addOptionList = fv.children.map((prop) => {
          const aliasProp = prop.value ? prop.value : prop.prop;
          const value = aliasProp.substring(aliasProp.lastIndexOf('.') + 1);
          return {
            prop: value,
            label: prop.label,
            fieldCode: value,
            fieldName: prop.label
          };
        });
      }
    }
  }

  handleMutexSelected(val: string, edit: boolean) {
    if (val !== '') {
      this.addOptionList = [];
      const fv = arrayFindDeep(this.flowVariables, 'value', val);
      if (fv && fv.children) {
        /**
         * 判断所选变量的视图id与主表是否一致
         * 如果一致，则采用默认匹配
         * 如果不一致，则开启匹配设置
         */
        if (fv.origId !== this.itemAttribute.origId) {
          if (!edit) {
            // 初始化字段匹配,目前只支持一次性设置
            const addViewDataLists = this.itemAttribute.options.map((col) => {
              return {
                prop: col.prop,
                label: col.label,
                fieldType: col.fieldType,
                bindProp: ''
              };
            });
            this.itemAttribute.addSetList.forEach((set) => {
              if (set.addSetType === 'addList') {
                addViewDataLists.push(
                  ...set.colsAdd.map((col) => {
                    return {
                      prop: col.fieldCode,
                      label: col.fieldName,
                      fieldType: col.fieldType,
                      bindProp: ''
                    };
                  })
                );
              }
            });

            this.currentAddSet.addViewDataLists = addViewDataLists;
          }

          this.addOptionList = fv.children.map((prop) => {
            const aliasProp = prop.value ? prop.value : prop.prop;
            const value = aliasProp.substring(aliasProp.lastIndexOf('.') + 1);
            return {
              prop: value,
              label: prop.label,
              fieldCode: value,
              fieldName: prop.label
            };
          });
        } else {
          this.currentAddSet.addViewDataLists = [];
        }
      }
    }
  }
  addTableList(val: any) {
    this.currentAddSet.colsAdd = [];
    const colsAdd: Array<any> = [];
    val.forEach((col: any) => {
      const field = arrayFind(this.addOptionList, 'fieldCode', col);
      if (field) {
        colsAdd.push({
          fieldCode: field.fieldCode,
          fieldName: field.fieldName,
          fieldType: field.fieldType,
          prop: field.fieldCode,
          label: field.fieldName,
          align: 'center'
        });
      }
    });
    this.currentAddSet.colsAdd = colsAdd;
  }
  // 添加列确定
  confirmExtendSet() {
    this.extendColDetail = false;
    if (this.currentAddSet.addSetType !== 'bottonSet') {
      if (this.currentAddSet.addSetType !== 'addList') {
        this.addTableList(this.currentAddSet.addDataValue);
      }
      if (this.extendColFlag === 'add') {
        this.itemAttribute.addSetList.push(this.currentAddSet);
        this.itemAttribute.cols.push(
          ...this.currentAddSet.colsAdd.map((col) => {
            return {
              prop: col.fieldCode,
              label: col.fieldName,
              ...col
            };
          })
        );
      } else {
        this.itemAttribute.addSetList.splice(
          this.modifyIndex,
          1,
          this.currentAddSet
        );
        this.itemAttribute.cols = this.polymerizeTableCols(
          this.addOptionList,
          this.currentAddSet.colsAdd,
          cloneDeep(this.itemAttribute.cols)
        );
      }
    } else {
      if (this.extendColFlag === 'add') {
        this.itemAttribute.addSetList.push(this.currentAddSet);
        this.itemAttribute.cols.push(
          ...this.currentAddSet.buttonAdd.map((col) => {
            return {
              prop: col.fieldCode,
              label: col.fieldName,
              ...col
            };
          })
        );
      } else {
        this.itemAttribute.addSetList.splice(
          this.modifyIndex,
          1,
          this.currentAddSet
        );
        this.itemAttribute.cols = this.polymerizeTableCols(
          [],
          this.currentAddSet.buttonAdd,
          cloneDeep(this.itemAttribute.cols)
        );
      }
      // this.btnColsConfirm();
    }
  }
  btnColsConfirm() {
    let operationCol = arrayFind(this.itemAttribute.cols, 'prop', 'operation');
    if (!operationCol) {
      operationCol = {
        prop: 'operation',
        label: '操作',
        fieldCode: 'operation',
        fieldName: '操作',
        buttonAdd: []
      };
      this.itemAttribute.cols.push(operationCol);
    }
    if (this.extendColFlag === 'add') {
      this.itemAttribute.addSetList.push(this.currentAddSet);
      operationCol.buttonAdd.push(...this.currentAddSet.buttonAdd);
    } else {
      this.itemAttribute.addSetList.splice(
        this.modifyIndex,
        1,
        this.currentAddSet
      );
      operationCol.buttonAdd = this.polymerizeTableCols(
        [],
        this.currentAddSet.buttonAdd,
        operationCol.buttonAdd
      );
    }
  }
  // 关联显示-设置
  defaultShow(tableItem, index) {
    if (tableItem) {
      this.tableShowConfig = {
        index: index,
        tableItem: tableItem
      };
      if (!tableItem.showValueConfig) {
        this.showValueConfig = {
          showType: 'form',
          valueShow: '',
          targetType: 'base',
          bindField: '',
          symbolShow: '1',
          inputValue: '',
          relationship: '2',
          parameterList: []
        };
      } else {
        this.showValueConfig = cloneDeep(tableItem.showValueConfig);
      }
    } else {
      this.tableShowConfig = {};
      this.valueList();
      if (!this.itemAttribute.showValueConfig) {
        // 临时代码，将原在itemAttribute中的配置迁移到showValueConfig中
        if (this.itemAttribute.valueShow) {
          this.showValueConfig = {
            showType: this.itemAttribute.showType,
            valueShow: this.itemAttribute.valueShow,
            targetType: this.itemAttribute.targetType,
            bindField: this.itemAttribute.bindField,
            symbolShow: this.itemAttribute.symbolShow,
            inputValue: this.itemAttribute.inputValue,
            parameterList: this.itemAttribute.parameterList,
            relationship: '2'
          };
          this.itemAttribute.valueShow = '';
        } else {
          this.showValueConfig = {
            showType: 'form',
            valueShow: '',
            targetType: 'base',
            bindField: '',
            symbolShow: '1',
            inputValue: '',
            parameterList: [],
            relationship: '2'
          };
        }
      } else {
        this.showValueConfig = cloneDeep(this.itemAttribute.showValueConfig);
        if (this.showValueConfig.isChildForm === true) {
          this.valueCascade = this.showValueConfig.valueShow.split(',');
          this.showValueConfig.valueShow = '';
        }
      }
      if (this.showValueConfig.valueShow) {
        this.handleCTagetSelect(
          this.showValueConfig,
          this.showValueConfig.bindTarget,
          true
        );
      }
    }
    this.showVisible = true;
  }

  clearShowConfig(item, index) {
    this.$confirm('确定要清除显示条件吗?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning',
      center: true
    })
      .then(() => {
        if (item) {
          this.$set(
            this.itemAttribute.addSetList[index],
            'showValueConfig',
            ''
          );
        } else {
          if (this.itemAttribute.showValueConfig) {
            this.itemAttribute.showValueConfig = null;
          }
          if (this.itemAttribute.valueShow) {
            this.itemAttribute.valueShow = '';
            this.itemAttribute.symbolShow = '1';
            this.itemAttribute.showType = 'form';
            this.itemAttribute.inputValue = '';
            this.itemAttribute.parameterList = [];
          }
        }
      })
      .catch(() => {});
  }
  tableDatafilterShow() {
    if (this.itemAttribute.filterConfig) {
      this.filterConfig = cloneDeep(this.itemAttribute.filterConfig);
    } else {
      this.filterConfig = {
        cfgType: 'bind',
        valueShow: '',
        parameterList: [],
        filterText: [],
        strategy: 'and'
      };
      // 临时代码，替换历史配置
      if (
        this.itemAttribute.filterText &&
        this.itemAttribute.filterText.length > 0
      ) {
        this.filterConfig.filterText = cloneDeep(this.itemAttribute.filterText);
        this.filterConfig.strategy = 'and';
        this.itemAttribute.filterText = null;
      }
    }
    this.tableFilterDialogVisible = true;
  }
  clearFilterConfig() {
    this.$confirm('确定要清除数据过滤条件吗?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning',
      center: true
    })
      .then(() => {
        this.itemAttribute.filterText = null;
        this.itemAttribute.filterConfig = null;
      })
      .catch(() => {});
  }
  clearShowValueConfig() {
    this.$confirm('确定要清除关联显示条件吗?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning',
      center: true
    })
      .then(() => {
        this.itemAttribute = Object.assign({}, this.itemAttribute, {
          showValueConfig: null
        });
      })
      .catch(() => {});
  }
  clearDvConfig() {
    this.$confirm('确定要清除默认值设置吗?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning',
      center: true
    })
      .then(() => {
        if (this.itemAttribute.type === 'childFrom') {
          this.itemAttribute.value = {};
        }
        this.itemAttribute.defaultValue = '';
      })
      .catch(() => {});
  }
  // 关联显示-判断显示
  symbolShowSearch() {
    let typeChange: any = '';
    typeChange = arrayFind(
      this.valueShowList,
      'value',
      this.itemAttribute.valueShow
    ).type;
    this.symbolShowList.forEach((item) => {
      if (typeChange !== 'inputNumber' || typeChange !== 'date') {
        item.disabled = false;
      }
    });
  }
  // 关联显示—设置确定
  showComform() {
    this.showVisible = false;
    if (JSON.stringify(this.tableShowConfig) !== '{}') {
      this.$set(
        this.itemAttribute.addSetList[this.tableShowConfig.index],
        'showValueConfig',
        this.showValueConfig
      );
    } else {
      this.itemAttribute.showValueConfig = this.showValueConfig;
    }
  }
  // 关联显示—设置取消
  showDialog() {
    this.showVisible = false;
  }
  getDataPushSet() {
    dataViewFields({ type: '1' }).then(({ data, error }) => {
      if (data) {
        this.linkedSerDataList = data;
        // this.viewDataSet = data;
      } else {
        this.$message.error(error.message);
      }
    });
  }
  confirmTableFilter() {
    this.itemAttribute.filterConfig = this.filterConfig;
    this.tableFilterDialogVisible = false;
  }
  cancelTableFilter() {
    this.tableFilterDialogVisible = false;
  }
  handleOptionNameChange(record) {
    record.value = record.label;
  }
  scopeForm(result) {
    if (result) {
      const optionList: Array<any> = this.itemAttribute.options
        ? cloneDeep(this.itemAttribute.options)
        : [];
      const addList: Array<any> = [];
      this.itemAttribute.addSetList.forEach((item) => {
        if (
          item.addSetType === 'addList' &&
          item.extendColName !== this.currentAddSet.extendColName
        ) {
          Array.prototype.push.apply(addList, item.colsAdd);
        }
      });

      // 添加行主键支持
      optionList.push({
        fieldCode: 'editMarkIndex',
        prop: 'editMarkIndex',
        fieldType: 0,
        fieldName: '行数据主键',
        label: '行数据主键'
      });

      this.addSourceOptionList = optionList.concat(addList);
    }
  }
  filterType(result, scopeRow) {
    if (result) {
      const filterLinkMethod = arrayFind(
        this.itemAttribute.cols,
        'prop',
        scopeRow.textLinkValue
      );
      // 0：字符串，1数字，2日期，3布尔
      // 如果是数字和日期，则允许使用大于、小于、等于、不等于；如果是字符串，则允许使用等于、不等于、包含、不包含。

      this.symbolShowListIndex.forEach((item) => {
        item.disable = true;
        if (filterLinkMethod.fieldType === 0) {
          if (
            item.value === '1' ||
            item.value === '3' ||
            item.value === '6' ||
            item.value === '7'
          ) {
            item.disable = false;
          }
        } else if (
          filterLinkMethod.fieldType === 1 ||
          filterLinkMethod.fieldType === 2
        ) {
          if (
            item.value === '0' ||
            item.value === '1' ||
            item.value === '2' ||
            item.value === '3'
          ) {
            item.disable = false;
          }
        } else {
          item.disable = false;
        }
      });
    }
  }
  // 静态列显示隐藏
  staticColumnSave() {
    this.staticColumn = false;
    this.scopeRow.staticList = this.staticList;
  }
  addStaticList(row) {
    this.staticColumn = true;
    if (!row.staticList) {
      this.$set(row, 'staticList', {
        staticType: 'tableSta',
        staticDataList: [],
        showValueConfig: {
          showType: 'tableSta',
          valueShow: '',
          targetType: 'base',
          bindField: '',
          symbolShow: '1',
          inputValue: '',
          relationship: '2',
          parameterList: [],
          staticType: 'tableCols'
        },
        fieldCode: row.fieldCode
      });
      this.staticList = row.staticList;
    } else {
      this.staticList = cloneDeep(row.staticList);
    }
    this.scopeRow = row;
  }
  recordQueryField(fieldCode) {
    if (
      this.itemAttribute.type === 'select' &&
      this.itemAttribute.sourceDataType === 'dataView'
    ) {
      if (fieldCode) {
        if (!this.itemAttribute.linkFields) {
          this.itemAttribute.linkFields = [];
        }
        if (this.itemAttribute.linkFields.indexOf(fieldCode) === -1) {
          this.itemAttribute.linkFields.push(fieldCode);
        }
      }
    }
  }
  sourceDataTypeClick() {
    if (this.itemAttribute.sourceDataType == 'dataView') {
      this.dataSetChange(this.itemAttribute.dataSetVal);
    } else if (this.itemAttribute.sourceDataType == 'variable') {
      this.handleFlowVariable(this.itemAttribute.flowVisibleCode);
    }
  }
  created() {
    this.fromList();
    this.getDataPushSet();
    this.valueList();
  }
}
