










































































































































import { Component, Vue, Prop, Emit } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
import vuedraggable from 'vuedraggable';

@Component({
  components: { vuedraggable }
})
export default class TableColumnsConfig extends Vue {
  @Prop() configForm: any;
  @Prop() formAttribute: any;

  private currentTable: any = {
    columns: []
  };
  colStatic: any = '';

  get config() {
    this.configForm.cols.forEach((item) => {
      if (!item.editType) {
        this.$set(item, 'editType', 'common');
      }
      if (!item.accuracy) {
        this.$set(item, 'accuracy', 2);
      }
    });
    return this.configForm;
  }

  // 改变表格字段顺序
  handleOrderChange() {
    this.currentTable.columns = this.config.cols;
    this.updateTableColumns();
  }

  // 删除表格字段
  deleteTableColumn(field: any) {
    if (this.currentTable && this.currentTable.columns) {
      const index = this.currentTable.columns.findIndex(
        (item: any) => item.code === field.code
      );
      this.currentTable.columns.splice(index, 1);
      this.configForm.cols.splice(index, 1);
      if (!this.currentTable.columns.length) {
        this.currentTable.dataSetId = '';
      }
      this.updateTableColumns();
    }
  }

  // 改变表格列对齐方式
  handleAlignChange(val: any, field: any) {
    if (this.currentTable && this.currentTable.columns) {
      this.currentTable.columns.map((item: any) => {
        if (item.fieldCode === field.fieldCode) {
          item.align = val;
        }
      });
    }
    // this.updateTableColumns();
  }

  handleEditableChange(val: any, field: any) {
    if (this.currentTable && this.currentTable.columns) {
      this.currentTable.columns.map((item: any) => {
        if (item.fieldCode === field.fieldCode) {
          item.align = val;
        }
      });
    }
    this.updateTableColumns();
  }

  // 改变表格格式化方式
  handleFormatChange(val: any, field: any) {
    if (this.currentTable & this.currentTable.columns) {
      this.currentTable.columns.map((item: any) => {
        if (item.code === field.code) {
          item.format = val;
        }
      });
    }
    this.updateTableColumns();
  }
  scriptLinkConfig(val) {
    this.$emit('doScript', val);
  }
  clearScriptLinkConfig(val) {
    val.defaultList = null;
  }
  staticEditConfig(val) {
    this.$emit('doEditConfig', val);
  }
  clearStaticEditConfig(val) {
    val.staticList = null;
  }
  /**
   * 设置表格的表单项设置
   * @param   {[type]}  val  [val description]
   */
  setTableFormConfig(val): void {
    this.$emit('doEditTableFormConfig', val);
  }
  /**
   * 清空表格的表单项设置
   * @param   {[object]}  val  [val 表单项的设置]
   */
  clearTableFormConfig(val): void {
    val.tableFormConfig = null;
  }
}
