<template>
  <div class="form-display">
    <vuedraggable
      v-model="formAttribute.formList"
      class="wrapper"
      :move="move"
      @change="change"
      @start="start"
      @end="end"
    >
      <transition-group>
        <el-col
          v-for="(item, index) in formAttribute.formList"
          :key="item"
          :span="item.span"
          class="itemBox"
        >
          <!-- 表单选项 -->
          <div style="position:relative">
            <div
              class="item"
              :class="[item.key === itemAttribute.key ? 'activeItem' : '']"
              @click="formItemClick(item)"
            >
              <el-form-item
                :label="
                  item.type !== 'button'
                    ? item.label + formAttribute.labelSuffix
                    : ''
                "
                :prop="'formList.' + index + '.' + item.value"
                rules="item.rules"
                style="margin-bottom: 10px"
                :required="item.required"
              >
                <!-- 表格:data-source="item.datas" -->
                <base-table
                  v-if="item.type === 'table'"
                  class="formTable"
                  :data-source="[]"
                  :columns="item.cols"
                ></base-table>
                <!-- 表单选项 -->
                <ueditor
                  v-else-if="item.type === 'ueditor'"
                  :itemValue="item"
                  class="ueditor"
                ></ueditor>
                <el-upload
                  v-else-if="item.type === 'upload'"
                  class="upload-demo"
                  list-type="picture-card"
                >
                  <i class="el-icon-plus"></i>
                </el-upload>
                <el-alert
                  class="alertStyle"
                  v-else-if="item.type === 'alert'"
                  :title="item.value"
                  :type="item.alertType"
                  :center="item.centerFont"
                  :closable="false"
                  :description="item.description"
                  :show-icon="item.showIcon"
                >
                </el-alert>
                <el-button
                  v-else-if="item.type === 'button'"
                  :type="item.buttonType"
                  >{{ item.value }}</el-button
                >
                <customItemRender
                  v-else
                  :type="item.type"
                  :config="{
                    placeholder: item.placeholder || '',
                    key: item.key,
                    value: item.value,
                    goods: item,
                    options: item.options,
                    multiple: item.multiple
                  }"
                />
              </el-form-item>
            </div>
            <div class="itemCover" @click="formItemClick(item)"></div>
            <div v-if="item.key === itemAttribute.key" class="itemButton">
              <el-button
                class="itemOperatButton copyButton"
                icon="el-icon-document-copy"
                @click="itemCopyClick(item, index)"
              >
              </el-button>
              <el-button
                class="itemOperatButton deleteButton"
                icon="el-icon-delete"
                @click="itemDeleteClick(index, formAttribute.formList)"
              >
              </el-button>
            </div>
          </div>
        </el-col>
      </transition-group>
    </vuedraggable>
  </div>
</template>

<script>
import cloneDeep from 'lodash/cloneDeep';
import vuedraggable from 'vuedraggable';
import customItemRender from '@components/renders/customItemRender.js';
import ueditor from './ueditor.vue';
export default {
  name: 'FormDisplay',
  components: {
    vuedraggable,
    customItemRender,
    ueditor
  },
  props: {
    formAttribute: {
      type: Object,
      default: () => {}
    },
    itemAttribute: {
      type: Object,
      default: () => {}
    },
    draggingArea: {
      type: String,
      default: () => ''
    },
    generateFormItem: {
      type: Function,
      default: () => function() {}
    },
    draggingAreaType: {
      type: String,
      default: () => ''
    }
  },
  data() {
    return {
      type: ''
    };
  },
  // mounted() {
  //  setInterval(()=>{
  //    console.log(this.formAttribute);
  //  },5000)
  // },
  methods: {
    stopBubble(e) {
      // 如果提供了事件对象，则这是一个非IE浏览器
      if (e && e.stopPropagation)
        // 因此它支持W3C的stopPropagation()方法
        e.stopPropagation();
      // 否则，我们需要使用IE的方式来取消事件冒泡
      else window.event.cancelBubble = true;
    },
    // 表单项点击事件
    formItemClick(item) {
      this.$emit('activeItemChange', item);
      // 阻止冒泡
      this.stopBubble();
    },
    // 表单项删除
    itemDeleteClick(index, rows) {
      if (rows.length > 1) {
        if (index === rows.length - 1) {
          this.$emit('activeItemChange', rows[rows.length - 2]);
        } else {
          this.$emit('activeItemChange', rows[index + 1]);
        }
      } else {
        this.$emit('activeItemChange', { key: '', bindValue: {} });
      }
      rows.splice(index, 1);
    },
    // 表单项复制
    itemCopyClick(item, index) {
      const copyItem = cloneDeep(item);
      const keyRandom = Math.random()
        .toString(36)
        .substring(2);
      this.$set(copyItem, 'key', keyRandom);
      if (copyItem.columns) {
        // 将分组项的表单项的key替换
        copyItem.columns.forEach((item) => {
          const keyRandomItem = Math.random()
            .toString(36)
            .substring(2);
          item.key = keyRandomItem;
        });
      }
      this.formAttribute.formList.splice(index + 1, 0, copyItem);
      this.$emit('activeItemChange', copyItem);
    },
    // 分组表单项复制
    groupItemCopyClick(paraentIndex, item, index) {
      const copyItem = cloneDeep(item);
      const keyRandom = Math.random()
        .toString(36)
        .substring(2);
      this.$set(copyItem, 'key', keyRandom);
      this.formAttribute.formList[paraentIndex].columns.splice(
        index + 1,
        0,
        copyItem
      );
      this.$emit('activeItemChange', copyItem);
    },
    // 分组清空表单项
    itemBrushClick(index, rows) {
      rows[index].columns = [];
    },
    // 分组拖拽
    handleSomethingEnterGroup() {
      this.$emit('draggingChange', 'draggingGroup');
    },
    groupMove(evt) {},
    groupChange(evt) {},
    groupStart(evt) {},
    groupendEnd(evt) {
      this.$emit('draggingChange', 'draggingViews');
    },
    change(evt) {},
    // start ,end ,add,update, sort, remove 得到的都差不多
    start(evt) {
      this.$emit('draggingChange', 'draggingViews');
    },
    end(evt) {
      // evt.item可以知道拖动的本身
      // evt.to可以知道拖动的目标列表
      // evt.from可以知道之前的列表
      // evt.oldIndex可以知道拖动前的位置
      // evt.newIndex可以知道拖动后的位置
      // console.log('end', evt);
    },
    move(evt, originalEvent) {
      // console.log('move', evt, originalEvent); // originalEvent鼠标位置
    }
  }
};
</script>
<style lang="scss">
.formTable {
  .table-wrap {
    padding-top: 0 !important;
  }
}
.ueditor {
  .ql-container {
    min-height: 76px;
  }
}
.item {
  .el-input--small {
    height: 32px;
  }
}
.alertStyle {
  .el-alert__description {
    line-height: 16px !important;
  }
}
.form-display {
  .el-upload--picture-card {
    width: 32px;
    height: 32px;
    line-height: 32px;
    i {
      font-size: 16px;
    }
  }
  .el-transfer {
    .el-transfer-panel__body {
      height: 180px;
    }
  }
}
</style>
<style lang="scss" scoped>
.wrapper {
  padding: 0 5px;
  .itemBox {
    position: relative;
    min-height: 60px;
    line-height: 60px;
  }
  .item {
    position: relative;
    // width: 95%;
    // height: 40px;
    padding: 10px 20px;
    // line-height: 40px;
    color: #fff;
    cursor: move;
    border-left: 3px solid $color-back-card;
  }
  .activeItem {
    background: $color-form-hover;
    border-left: 3px solid $color-main !important;
  }
  .activeGroupItem {
    background: #ecf5ff;
    border: 0.5px solid $color-main !important;
  }
  .groupBox {
    position: relative;
    min-height: 68px;
    padding-bottom: 80px;
    .groupButton {
      position: absolute;
      bottom: -29px;
      right: 20px;
    }
  }
  .groupBox:hover {
    border: 1px dashed $color-main;
  }
  .itemCover {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    width: 100%;
    min-height: 62px;
    cursor: move;
  }
  .itemButton {
    position: absolute;
    right: 25px;
    bottom: -27px;
    z-index: 5;
  }
  .itemOperatButton {
    width: 30px;
    height: 30px;
    padding: 0;
    line-height: 30px;
    border: 1px solid #ccc;
    border-radius: 15px;
  }
  .copyButton {
    color: $color-main;
    background: #ecf5ff;
    border-color: #b3d8ff;
  }
  .copyButton:hover {
    color: #fff;
    background: $color-main;
  }
  .brushButton {
    color: #e6a23c;
    background: #fdf6ec;
    border-color: #f5dab1;
  }
  .brushButton:hover {
    color: #fff;
    background: #e6a23c;
  }
  .deleteButton {
    color: #f56c6c;
    background: #fef0f0;
    border-color: #fbc4c4;
  }
  .deleteButton:hover {
    color: #fff;
    background: #f56c6c;
  }
}
</style>
